    function parseFormat(parsestring, parseJson, type) {
        if (type === "TR") {
            const dataMap = parseJson.map(function (object, index, arr) {
                console.log(object)
                const parsrarr = object.ParseValueTRT
                // console.log(parsrarr)
                if (parsrarr.length === 0) {
                    return {
                        item: object.item,
                        Status: false
                    }
                }
                const findvlaue = parsrarr.some(function (v, i, a) {
                    return parsestring.match(v) !== null
                })
                return {
                    item: object.item,
                    Status: findvlaue
                }
            })
            return dataMap
        }
        else {
            const dataMap = parseJson.map(function (object, index, arr) {
                const parsrarr = object.ParseValueUD
                if (parsrarr.length === 0) {
                    return {
                        item: object.item,
                        Status: false
                    }
                }
                const findvlaue = parsrarr.some(function (v, i, a) {
                    return parsestring.match(v) !== null
                })
                return {
                    item: object.item,
                    Status: findvlaue
                }
            })
            return dataMap
        }
    }
